.Modal__backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.5);
}

.Modal__content {
  position: absolute;
  top: 54%;
  left: 50%;
  transform: translate(-50%, -50%);
  min-height: 500px;
  max-width: 800px;
  width: 100%;
}
