.LoaderPart {
  object-fit: cover;
  border-radius: 80%;

  position: absolute;
  // top: calc(50% - 4em);

  left: calc(50% - 2em);
  width: 5em;
  height: 5em;
  border: 1.1em solid rgba(0, 0, 0, 0.2);
  border-left: 1.1em solid #000000;
  border-radius: 50%;
  animation: load8 1.1s infinite linear;
  transition: opacity 0.3s;
}
